.todo-form__add-btn {
    color: white;
    padding: .5rem 1.5rem;
    background: gray;

    border-radius: .6rem;

    border: 2px solid silver;

    cursor: pointer;
    transition: all .3s;

}

.todo-form__add-btn:hover {
    background: #2b2727;
    border: 2px solid gray;
}

.todo-form__add-btn:disabled {
    opacity: .5;
    pointer-events: none;
}

@media (max-width: 850px) {

    .todo-form__add-btn {
        width: 100%;
    }
    
}